/*! dgsnackbar v2.0.0-rc2 | (c) 2020  | MIT License | https://github.com/dgnest/dgsnackbar */
module.exports = (function(window, document){
  'use strict';

  function extend(target) {
    var sources = [].slice.call(arguments, 1);
    sources.forEach(function (source) {
        for (var prop in source) {
            target[prop] = source[prop];
        }
    });
    return target;
  }

  /* Code from Modernizr, thanks Modernizr =) */
  function whichTransitionEvent(){
    var t;
    var el = document.createElement('fakeelement');
    var transitions = {
      'transition':'transitionend',
      'OTransition':'oTransitionEnd',
      'MozTransition':'transitionend',
      'WebkitTransition':'webkitTransitionEnd'
    };

    for(t in transitions){
      if( el.style[t] !== undefined ){
        return transitions[t];
      }
    }
  }

  // Default Options
  var defautltOptions = {
    message: 'Este es mi dgsnackbar',
    duration: 5000,
    type: 'deafult'
  };

  // Core
  /**
   * dgsnackbar constructor
   * @constructor
   * @param {Object} options - options for the snackbar
   * @param {string} options.message - message for the snackbar
   * @param {number} options.duration - time in ms to close snackbar automatically (default: 5000)
   * @param {string} options.type - type of snackbar (default, success, warning, error)
   */
  return function(options) {
    if ( typeof options === 'string' ) {
      options = {
        message: options,
      };
    }

    // Extend Options
    options = extend(defautltOptions, options);

    var $container = document.getElementsByClassName('dgsnackbar')[0];
    var $dgsnackbar = document.createElement('div');

    // Set class to dgsnackbar
    var dgsnackbarClassName = 'dgsnackbar__surface';
    if (options.type === 'success') {
      dgsnackbarClassName += ' dgsnackbar__surface--success';
    } else if (options.type === 'error') {
      dgsnackbarClassName += ' dgsnackbar__surface--alert';
    } else if (options.type === 'warning') {
      dgsnackbarClassName += ' dgsnackbar__surface--warning';
    } else {
      dgsnackbarClassName += ' dgsnackbar__surface--' + options.type;
    }
    $dgsnackbar.className = dgsnackbarClassName;

    // Insert Message
    var $message = document.createElement('div');
    $message.className = 'dgsnackbar__label';
    $message.innerHTML = options.message;
    $dgsnackbar.appendChild($message);

    // Insert Buttons
    var $actions = document.createElement('div');
    $actions.className = 'dgsnackbar__actions';

    if (options.action) {
      var $actionBtn = document.createElement('button');
      $actionBtn.className = 'dgsnackbar__button dgsnackbar__action'
      $actionBtn.innerHTML = options.action;
      $actions.appendChild($actionBtn);

      $actionBtn.addEventListener('click', function(event) {
        this.dismiss();
        if (typeof options.onAction === 'function') {
          options.onAction();
        }
      }.bind($dgsnackbar));
    }

    var $dismissBtn = document.createElement('button');
    $dismissBtn.className = 'dgsnackbar__icon-button dgsnackbar__dismiss';
    var $closeIcon = document.createElement('div');
    $closeIcon.className = 'dgsnackbar__close';
    $dismissBtn.appendChild($closeIcon);
    $actions.appendChild($dismissBtn);

    $dgsnackbar.appendChild($actions);


    // Dismiss
    $dgsnackbar.dismiss = function() {
      this.style.opacity = 0;
      window.clearTimeout(this.timer);

      // Remove snackbar if already exist
      window.setTimeout(function() {
        if ($dgsnackbar && $dgsnackbar.parentNode) {
          $dgsnackbar.parentNode.removeChild($dgsnackbar);
        }
      }, 5000);
    };

    // Create container if not exist.
    if ( !$container ) {
      $container = document.createElement('DIV');
      $container.className = 'dgsnackbar dgsnackbar--open';

      document.body.appendChild($container);
    }

    $container.appendChild($dgsnackbar);

    // Remove $dgsnackbar when dismiss animation ended
    $dgsnackbar.addEventListener(whichTransitionEvent(), function(event) {
      if (event.propertyName === 'opacity' && this.style.opacity === '0') {
        this.parentElement.removeChild(this);
      }
    }.bind($dgsnackbar));

    $dismissBtn.addEventListener('click', function(event) {
      this.dismiss();
    }.bind($dgsnackbar));

    window.setTimeout(function() {
      $dgsnackbar.className += ' dgsnackbar__surface--open';
    }, 50);

    // Remove dgsnackbar after options duration
    $dgsnackbar.timer = window.setTimeout(function(){
      this.dismiss();
    }.bind($dgsnackbar), options.duration);
  };
})(window, document);